<template>
    <p-dialog
        v-model:visible="registerDialog"
        :style="{ width: '50vw' }"
        :modal="true"
        header="Register"
        class="p-inputtext-sm p-input-filled"
        @show="fillForm"
    >
        <div class="p-grid">
            <div class="p-col">
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        First Name
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.firstName"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        D.O.B
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.dob"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Address
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.address"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Province
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.province"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Patient Status
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.status"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Phone
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.phone"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Email
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.email"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
            </div>
            <div class="p-col">
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Last Name
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.lastName"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Demographic No
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.demographicNo"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        City
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.city"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Postal
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.postal"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Sex
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.sex"
                            type="text"
                            style="width: 100%"
                            disabled
                        />
                    </div>
                </div>
                <div class="p-field p-grid">
                    <label class="p-col-fixed" style="width:120px">
                        Password
                    </label>
                    <div class="p-col">
                        <p-input-text
                            v-model="form.password"
                            type="password"
                            style="width: 100%"
                            autofocus
                            :class="{
                                'p-invalid': submitted && v$.password.$invalid
                            }"
                        />
                    </div>
                </div>
            </div>
        </div>

        <template #footer>
            <p-button label="Cancel" @click="hideDialog" />
            <p-button label="Submit" autofocus @click="register" />
        </template>
    </p-dialog>
</template>

<script>
import { inject, reactive, ref } from "vue";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";

import { registerPatient } from "@/service/patient-message-log";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const registerDialog = inject("registerDialog");
        const selectedPaitent = inject("selectedPaitent");

        const form = reactive({
            demographicNo: null,
            firstName: null,
            lastName: null,
            dob: null,
            address: null,
            city: null,
            province: null,
            postal: null,
            status: null,
            sex: null,
            phone: null,
            email: null,
            password: null
        });

        const fillForm = () => {
            let patient = { ...selectedPaitent.value };

            form.demographicNo = patient.demographicNo;
            form.firstName = patient.firstName;
            form.lastName = patient.lastName;
            form.dob = patient.dob;
            form.address = patient.address;
            form.city = patient.city;
            form.province = patient.province;
            form.postal = patient.postal;
            form.status = patient.status;
            form.sex = patient.sex;
            form.phone = patient.phone;
            form.email = patient.email;
        };

        const rules = {
            phone: { required },
            password: { required }
        };
        const v$ = useVuelidate(rules, form);
        const submitted = ref(false);

        const toast = useToast();
        const isRegistered = inject("isRegistered");

        const register = () => {
            submitted.value = true;
            if (v$.value.$invalid) {
                return;
            }

            registerPatient({
                ...form,
                mobileNumber: form.phone,
                registerOrReset: isRegistered.value == true ? 2 : 1
            })
                .then((resp) => {
                    registerDialog.value = false;
                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Saved successfully",
                        life: 3000
                    });
                })
                .catch((err) => {
                    const msgs = err.response.data.errors.join("\n");
                    toast.add({
                        severity: "error",
                        summary: "Error",
                        detail: msgs,
                        life: 3000
                    });
                });
        };

        return {
            registerDialog,
            form,
            fillForm,
            submitted,
            v$,
            register,
            hideDialog: () => (registerDialog.value = false)
        };
    }
};
</script>
