<template>
    <!-- <div class="p-fluid p-formgrid p-grid p-input-filled p-jc-between">
        <div class="p-field p-col-12 p-md-4">
            <label for="patient-search">Patient Search</label>
            <div class="p-inputgroup">
                <p-auto-complete
                    v-model="patient"
                    :suggestions="filteredPatients"
                    @complete="searchPatient($event)"
                    @item-select="getPatient($event)"
                    field="fullName"
                    :class="{
                        'p-invalid': submitted && patientInvalid
                    }"
                />
                <span class="p-inputgroup-addon">
                    <i class="pi pi-search"></i>
                </span>
            </div>
        </div>
    </div> -->
    <div>
        <div
            class="p-fluid p-formgrid p-grid p-input-filled"
            v-if="hasSetup && isRegistered"
        >
            <div class="p-field p-col-12 p-md-4">
                <label>Subject</label>
                <p-input-text
                    v-model="form.subject"
                    :class="{
                        'p-invalid': submitted && v$.subject.$invalid
                    }"
                />
                <small
                    v-if="
                        (v$.subject.$invalid && submitted) ||
                            v$.subject.$pending.$response
                    "
                    class="p-error"
                    >{{ v$.subject.required.$message.replace("Value", "Subject") }}
                </small>
            </div>
            <div class="p-col-12 p-md-6"></div>
            <div class="p-field p-col-12 p-md-2" v-if="hasSetup && isRegistered">
                <label>-</label>
                <br />
                <p-button label="Reset Password" @click="showRegisterDialog" />
            </div>
            <div class="p-field p-col-12 p-md-6">
                <label>Message</label>
                <p-textarea
                    v-model="form.message"
                    rows="5"
                    :class="{
                        'p-invalid': submitted && v$.message.$invalid
                    }"
                />
                <small
                    v-if="
                        (v$.message.$invalid && submitted) ||
                            v$.message.$pending.$response
                    "
                    class="p-error"
                    >{{ v$.message.required.$message.replace("Value", "Message") }}
                </small>
            </div>
        </div>
        <p-button
            label="Send"
            class="p-mr-2"
            @click="send"
            v-if="hasSetup && isRegistered"
        />
        <p-button label="Print Message History" v-if="hasSetup && isRegistered" />

        <div
            class="p-fluid p-formgrid p-grid p-input-filled"
            v-if="!hasSetup || !isRegistered"
        >
            <div class="p-field p-col-12 p-md-8">
                <h1 style="color:red;">PATIENT IS NOT REGISTERED FOR MESSAGING</h1>
            </div>

            <div class="p-field p-col-12 p-md-4" v-if="hasSetup && !isRegistered">
                <label>Click to register the patient for messaging</label>
                <br />
                <p-button
                    label="Register"
                    style="width: 100px"
                    @click="showRegisterDialog"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { computed, inject, onMounted, reactive, ref } from "vue";
import {
    composeMessage,
    getPatientInformation,
    searchAllPatient
} from "@/service/patient-message-log";
import { useToast } from "primevue/usetoast";
import { useStore } from "vuex";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();

        const registerDialog = inject("registerDialog");
        const isRegistered = inject("isRegistered");
        const hasSetup = inject("hasSetup");
        const selectedPaitent = inject("selectedPaitent");

        const patient = ref();
        const patientInvalid = computed(() => !patient.value);
        const filteredPatients = ref([]);

        const form = reactive({
            subject: null,
            message: null
        });
        const rules = {
            subject: { required },
            message: { required }
        };
        const v$ = useVuelidate(rules, form);
        const submitted = ref(false);

        const messageHistories = inject("messageHistories");
        const send = async () => {
            submitted.value = true;
            if (v$.value.$invalid && patientInvalid) {
                return;
            } else {
                const { newMessage } = await composeMessage({
                    ...form,
                    sendTo: selectedPaitent.value.regId,
                    sentBy: store.state.user.providerNo,
                    senderType: 1,
                    parent: 0
                });

                form.subject = null;
                form.message = null;
                submitted.value = false;

                emitter.emit("patient-message-log:get-message");
            }
        };

        const searchPatient = async (event) => {
            const patients = await searchAllPatient(event.query.trim());
            filteredPatients.value = patients;
        };

        const toast = useToast();

        const getPatient = async (id) => {
            try {
                const {
                    patientData,
                    resgistered,
                    regId
                } = await getPatientInformation(id);

                selectedPaitent.value = {
                    ...patientData,
                    demographicNo: patientData.demographic_no,
                    firstName: patientData.first_name,
                    lastName: patientData.last_name,
                    status: patientData.patient_status,
                    name: `${patientData.first_name} ${patientData.last_name}`,
                    regId: regId
                };
                isRegistered.value = resgistered;
                hasSetup.value = true;
                emitter.emit("patient-message-log:get-message");
            } catch (error) {
                if (error.response.status == 404) {
                    toast.add({
                        severity: "error",
                        summary: "Error",
                        detail: "The Patient do not have Demo Cell Setup",
                        life: 3000
                    });
                }
            }
        };

        onMounted(() => {
            getPatient(store.state.artCycle.patient.id);
        });

        return {
            isRegistered,
            hasSetup,
            patient,
            getPatient,
            form,
            v$,
            submitted,
            send,
            selectedPaitent,
            patientInvalid,
            filteredPatients,
            searchPatient,
            showRegisterDialog: () => (registerDialog.value = true)
        };
    }
};
</script>
<style scoped>

