<template>
    <div>
        <h4>
            Current Messge History -
            <p-input-text
                v-model="limit"
                @update:modelValue="limitInputHandler"
                style="width: 60px;"
                type="number"
            />
            Items

            <!-- <p-button
                v-if="!displaySMS"
                label="Direct SMS List"
                @click="changetoSMSView(true)"
                class="p-mr-2 p-button-primary p-button-sm"
            /> -->

            <!-- <p-button
                v-if="displaySMS"
                label="Portal Msg List"
                @click="changetoSMSView(false)"
                class="p-mr-2 p-button-primary p-button-sm"
            /> -->
        </h4>

        <p-data-table
            :value="messageHistories"
            v-if="!displaySMS"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mb-1 message-table"
            selectionMode="single"
            :rowClass="getRowClass"
        >
            <p-column header="Date" field="SentDate" style="width: 150px" />
            <p-column header="Subject" field="Subject" />
            <p-column header="Read" field="Unread" style="width: 60px">
                <template #body="{data}">
                    {{ data.Unread == 0 ? "Y" : "N" }}
                </template>
            </p-column>
            <p-column header="To" field="receiver" style="width: 200px" />
            <p-column header="From" field="sender" style="width: 200px" />
            <p-column header="Message" field="Message" />
        </p-data-table>

        <p-data-table
            :value="smsHistories"
            v-else
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mb-1"
        >
            <p-column
                header="TimeStamp"
                field="time_stamp"
                style="width: 150px"
            />

            <p-column header="To" field="reciver" style="width: 200px" />
            <p-column header="From" field="sentBy" style="width: 200px" />
            <p-column header="Message" field="msg" />
            <p-column header="Status" field="s_status" />
        </p-data-table>
    </div>
</template>

<script>
import { inject, onMounted, onUnmounted, watch, ref } from "vue";
import {
    getPatientMessage,
    getPatientSMSList
} from "@/service/patient-message-log";
import { useToast } from "primevue/usetoast";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const messageHistories = inject("messageHistories");
        const smsHistories = ref([]);

        const limit = ref(50);
        const displaySMS = ref(false);

        const selectedPaitent = inject("selectedPaitent");
        watch(selectedPaitent, (newValue) => {
            getMessages(newValue.regId, newValue.name);
        });

        const setupWebsocket = () => {
            const websocket = inject("websocket");
            websocket.onmessage = (event) => {
                const data = JSON.parse(event.data);
                if (typeof data.data == "string") {
                    const message = JSON.parse(data.data);
                    if (message.terminal == "nlp") {
                        let name = `${store.state.artCycle.patient.firstName} ${store.state.artCycle.patient.lastName}`;
                        getMessages(selectedPaitent.value.regId, name);
                    }
                }
            };

            websocket.onerror = () => {
                toast.add({
                    severity: "warn",
                    summary: "Server Down",
                    detail: "Instant chat is off at the moment",
                    life: 3000
                });
            };
        };

        const toast = useToast();
        const getMessages = async (id, name) => {
            if (!displaySMS.value) {
                try {
                    const { PatientMessages } = await getPatientMessage(
                        id,
                        name,
                        limit.value
                    );
                    messageHistories.value = PatientMessages;
                } catch (err) {
                    if (err.response.status == 404) {
                        messageHistories.value = [];
                        toast.add({
                            severity: "warn",
                            summary: "No Message",
                            detail: "No message found for this patient",
                            life: 3000
                        });
                    }
                }
            } else {
                try {
                    const { PatientMessages } = await getPatientSMSList(
                        id,
                        name,
                        limit.value
                    );
                    smsHistories.value = PatientMessages;
                } catch (err) {
                    console.log(err);
                    if (err.response.status == 404) {
                        smsHistories.value = [];
                        toast.add({
                            severity: "warn",
                            summary: "No Message",
                            detail: "No message found for this patient",
                            life: 3000
                        });
                    }
                }
            }
        };

        const changetoSMSView = async (val) => {
            displaySMS.value = val;

            let name = `${store.state.artCycle.patient.firstName} ${store.state.artCycle.patient.lastName}`;
            getMessages(selectedPaitent.value.regId, name, limit.value);
        };

        const getMessagesHandler = () => {
            let name = `${store.state.artCycle.patient.firstName} ${store.state.artCycle.patient.lastName}`;
            getMessages(selectedPaitent.value.regId, name);
        };

        onMounted(() => {
            emitter.on("patient-message-log:get-message", getMessagesHandler);
            setupWebsocket();
        });

        onUnmounted(() => {
            emitter.off("patient-message-log:get-message", getMessagesHandler);
        });

        return {
            messageHistories,
            smsHistories,
            displaySMS,
            changetoSMSView,
            limit,
            limitInputHandler: function() {
                let name = `${store.state.artCycle.patient.firstName} ${store.state.artCycle.patient.lastName}`;
                getMessages(selectedPaitent.value.regId, name, limit.value);
            },
            getRowClass(data) {
                let resp = {
                    "row-green": data.Subject === "Recevied-SMS",
                    "row-yellow": data.Subject === "Sent-SMS"
                };

                return resp;
            },
        };
    }
};
</script>

<style scoped>
:deep() .row-green {
    background-color: #bff1b0 !important;
}

:deep() .row-yellow {
    background-color: #f9f8d0 !important;
}

.message-table {
    margin-top: 20px;
}

@media (max-width: 768px) {
    .message-table {
        width: 100%;
        overflow-x: auto;
    }

    :deep(.p-datatable-thead th) {
        white-space: nowrap;
    }

    :deep(.p-datatable-tbody tr td) {
        white-space: nowrap;
    }
}

</style>
